.pagination{
    display:flex;
    justify-content: flex-start;
    list-style:none;
    cursor:pointer;
  }
  
  .pagination a{
    padding:10px;
    border:1px solid #47a7de;
    color:#47a7de
  }
  
  .pagination__link{
    font-weight:bold;
  }
  .pagination__link--active a{
    color:#fff;
    background:#47a7de;
  }
  
  .pagination__link--disabled a{
    color:rgba(198,197,202);
    border:1px solid rgb(198,197,202)
  }